:root {
  --primaryBlue: hsl(208, 100%, 34%);
  --darkBlue: hsl(207, 100%, 13%);
  --medBlue: hsl(208, 57%, 47%);
  --lightBlue: hsl(195, 66%, 71%) ;
  --mutedBlue: hsl(208, 33%, 60%) ;
  --menuShadow: hsla(208, 33%, 60%) ;
  --primaryGreen: hsl(92, 51%, 51%);
  --darkGreen: hsl(92, 50%, 20%);
  --medGreen: hsl(92, 51%, 66%);
  --lightGreen: hsl(79, 45%, 24%);
  --mutedGreen80: hsl(92, 37%, 71%, 80%);
  --primaryOrange: hsl(32, 94%, 60%);
  --darkOrange:	hsl(32, 62%, 38%);
  --medOrange: hsl(0, 0%, 0%);
  --lightOrange: hsl(32, 95%, 84%);
  --mutedOrange: hsl(32, 61%, 76%);
  --primaryYellow: hsl(49, 100%, 50%);
  --darkYellow:	hsl(49, 100%, 30%);
  --medYellow: hsl(49, 100%, 65%);
  --lightYellow: hsl(49, 100%, 80%);
  --mutedYellow: hsl(48, 54%, 70%);
  --primaryRed: hsl(347, 100%, 45%);
  --darkRed: hsl(347, 100%, 27%);
  --medRed:	hsl(347, 83%, 56%);
  --lightRed: hsl(347, 82%, 78%);
  --mutedRed: hsl(347, 54%, 67%);
  --advocacyRed: hsl(353, 100%, 18%);
  --weFinancial: hsl(193, 100%, 42%);
  --weFinancialDark: hsl(207, 100%, 26%);
  --header-bg: hsl(45, 22%, 96%);
  --menu-overlay: hsla(207, 100%, 13%, 40%);
  --lightGrey: hsl(0, 0%, 81%);
  --medGrey: hsl(0, 0%, 53%);
  --dkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 6%);
  --textBlack: hsl(0, 0%, 16%);
  --white: hsl(45, 22%, 96%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-heading: Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Atkinson Hyperlegible", Helvetica, Arial, sans-serif; /* body font */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  line-height: 1.1;
  text-wrap: balance;
  margin: 0 0 1.25rem;
}

h1 {
  font-size: 2.5rem;
  font-size: clamp(2.5rem, 0.9375rem + 3.35vw, 3.5rem);
}

h2 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 1.6875rem + 0.78vw, 2.625rem);
  font-weight: 500;
}

h3 {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.1875rem + 0.26vw, 1.5rem);
  font-weight: 500;
  margin-bottom: 0;
}

p {
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  text-wrap: balance;
  line-height: 1.5rem;
  margin-top: 0;
}

.large {
  font-size: 1.25rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.625rem;
}

.small {
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration-thickness: 1px;
  text-underline-offset: 4px;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
  shape-margin: 0.75rem;
}

figure,
blockquote {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

.btn {
  font-family: var(--font-text);
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 400;
  text-align: center;
  border-radius: 8px;
  padding: 8px 32px;
  margin: 0;
}
.btn:hover {
  cursor: pointer;
}
.btn__advocacy {
  color: var(--darkBlue);
  background-color: var(--white);
  border: 1px solid var(--white);
  display: inline-block;
}
.btn__advocacy:hover {
  color: var(--darkBlue);
  background-color: var(--primaryYellow);
  border: 1px solid var(--primaryYellow);
}
.btn__hhf {
  color: var(--white);
  background-color: var(--darkBlue);
  border: 1px solid var(--darkBlue);
  display: inline-block;
}
.btn__hhf:hover {
  color: var(--darkBlue);
  background-color: var(--white);
  border: 1px solid var(--darkBlue);
}
.btn__we {
  color: var(--white);
  background-color: var(--weFinancialDark);
  border: 1px solid var(--weFinancialDark);
  display: inline-block;
}
.btn__we:hover {
  color: var(--darkBlue);
  background-color: var(--weFinancial);
  border: 1px solid var(--weFinancial);
}

:root {
  --wrapper-padding-inline: 1rem;
  --wrapper-max-width: 1280px;
  --wrapper-padding-block: 32px;
}

.header {
  width: 100vw;
  max-height: 9rem;
  position: sticky;
  top: 0;
  background-color: var(--header-bg);
  box-shadow: 0 0 1.5rem var(--black);
  z-index: 50;
  position: fixed;
}
.header__site {
  width: 100%;
  height: 9rem;
}
@media (max-width: 29.99875em) {
  .header__site {
    height: 4rem;
  }
}

.site__branding {
  display: grid;
  grid-column: 1;
  grid-row: 1/4;
  z-index: 30;
  background-color: var(--primaryBlue);
  padding: 40px 15px 0 15px;
  height: 196px;
  width: 12.5rem;
  max-width: 196px;
  box-shadow: 0 0 10px var(--black);
  position: absolute;
}
@media (max-width: 29.99875em) {
  .site__branding {
    left: 24px;
  }
}
.site__branding a {
  grid-row: 4/4;
}
.site__navigation {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  height: 144px;
}

.topnav {
  width: 100vw;
  z-index: 900;
}
.topnav__container {
  position: relative;
  margin-inline: auto;
  max-width: var(--wrapper-max-width);
}
.topnav__wrapper {
  max-width: var(--wrapper-max-width);
  margin-inline: auto;
}
@media (max-width: 58.124375em) {
  .topnav__wrapper {
    justify-content: center;
  }
}
.topnav__branding {
  background-color: var(--primaryBlue);
  box-shadow: 0 0 1.5rem var(--black);
  position: absolute;
  top: 0;
  left: 24px;
  z-index: 999;
}
.topnav__homelink {
  display: inline-block;
}
.topnav__logo {
  padding: 72px 16px 12px;
}
.topnav__logo:hover {
  opacity: 0.8;
  transform: opacity 250ms ease-in-out;
}
.topnav__logo img {
  margin: 7rem 0 0.5rem;
  padding: 0 0.5rem;
}
@media (max-width: 29.99875em) {
  .topnav__overlay {
    position: fixed;
    inset: 0;
    background-color: var(--menu-overlay);
    opacity: 0;
    pointer-events: none;
    transition: opacity 400ms ease-in-out;
  }
  .topnav__overlay:has(+ .topnav__open[aria-expanded=true]) {
    opacity: 1;
    pointer-events: all;
  }
}
.topnav__open, .topnav__close {
  background: transparent;
  border: 0;
}
.topnav__open {
  position: fixed;
  margin-inline-start: 365px;
  padding-top: 1.25rem;
  display: none;
}
@media (max-width: 29.99875em) {
  .topnav__open {
    display: block;
  }
}
.topnav__open[aria-expanded=true] + .topnav__menu {
  translate: 0 0;
}
.topnav__close {
  display: none;
}
@media (max-width: 58.124375em) {
  .topnav__close {
    display: block;
    margin-inline-start: 365px;
    margin-block-end: 120px;
    padding-top: 1.25rem;
  }
}
@media (max-width: 29.99875em) {
  .topnav__menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    inset: 0 0 auto 0;
    background-color: var(--header-bg);
    border-radius: 0 0 20px 20px;
    width: 100vw;
    border-radius: 0 0 20px 20px;
    box-shadow: 0 0 1.5rem var(--black);
    translate: 0 -120%;
    transition: translate 400ms ease-in-out;
  }
}
.topnav__navigation {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 32px;
  margin: 0;
  padding: 0;
}
@media (max-width: 58.124375em) {
  .topnav__navigation {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 2px solid var(--primaryBlue);
    padding: 1.5rem;
  }
}
@media (max-width: 29.99875em) {
  .topnav__navigation {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--primaryBlue);
  }
}
.topnav__navLinks {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  margin: 0;
  padding: 16px 24px 16px 0;
  align-items: flex-end;
  max-height: 144px;
  overflow: visible;
  position: relative;
  z-index: 500;
}
@media (max-width: 58.124375em) {
  .topnav__navLinks {
    padding: 0 24px;
    max-height: 100%;
    align-items: center;
  }
}
@media (max-width: 29.99875em) {
  .topnav__navLinks {
    width: 100%;
  }
}
.topnav__navLinks--hidden {
  max-height: 6.25rem;
  align-items: flex-end;
}
.topnav__links {
  display: flex;
  flex-direction: row;
  gap: 32px;
  margin: 0;
  padding: 0;
}
@media (max-width: 58.124375em) {
  .topnav__links {
    padding: 1.875rem 0;
  }
}
@media (max-width: 29.99875em) {
  .topnav__links {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 1.875rem;
  }
}
.topnav__item {
  list-style-type: none;
}
.topnav__link {
  font-size: 1.125rem;
  font-weight: 400;
  letter-spacing: 0.05rem;
  color: var(--primaryBlue);
  text-decoration: none;
}
@media (max-width: 58.124375em) {
  .topnav__link {
    font-size: 1rem;
  }
}
.topnav__link:hover {
  color: var(--darkOrange);
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 6px;
  cursor: pointer;
}
.topnav__link--icon {
  margin-right: 10px;
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
}
.has-submenu:hover {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  padding: 0.0625rem;
  margin: 2.1875rem auto 0;
  width: 100vw;
  opacity: 1;
  visibility: visible;
}

.hero__section {
  background-color: var(--darkBlue);
}
@media (max-width: 29.99875em) {
  .hero__section {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 28.125rem;
  }
}
.hero__section--intro {
  margin-bottom: 5rem;
}
.hero__text {
  font-size: 3rem;
  font-size: clamp(3rem, 1.5rem + 5vw, 5.5rem);
  line-height: 1;
  letter-spacing: 0.5px;
  color: var(--white);
  border-left: 20px solid var(--white);
  padding-left: 25px;
  margin-bottom: 0;
  display: block;
}
@media (max-width: 29.99875em) {
  .hero__text {
    margin-top: 7.5rem;
  }
}
.hero__image {
  height: 100vh;
  object-fit: cover;
  margin-top: 6.25rem;
}

.hero.full-width-split-screen {
  padding-top: 144px;
}

.full-width-split-screen {
  padding-block: 0;
}

.full-width-split-screen > :not(img) {
  padding-block: var(--wrapper-padding-block);
  padding-inline: var(--wrapper-padding-inline);
}

@media (min-width: 930px) {
  .full-width-split-screen {
    display: grid;
    grid-template-columns: minmax(var(--wrapper-padding-inline), 1fr) minmax(0, calc(var(--wrapper-max-width) / 2)) minmax(0, calc(var(--wrapper-max-width) / 2)) minmax(var(--wrapper-padding-inline), 1fr);
    position: relative;
  }
  .full-width-split-screen > img:first-child {
    grid-column: 1/3;
  }
  .full-width-split-screen > img:last-child {
    grid-column: 3/-1;
  }
  .full-width-split-screen > :not(img) {
    display: grid;
    align-content: center;
    justify-items: start;
  }
  .full-width-split-screen > :not(img):first-child {
    grid-column: 2/3;
    padding-left: 1.5rem;
    padding-right: clamp(1rem, 5vw, 3rem);
  }
  .full-width-split-screen > :not(img):last-child {
    grid-column: 3/4;
    padding-right: 1.5rem;
    padding-left: clamp(1rem, 5vw, 3rem);
  }
  .full-width-split-screen > :not(img)::before {
    content: "";
    position: absolute;
    inset: 0;
    grid-row: 1/2;
    background-color: inherit;
  }
  .full-width-split-screen > :not(img):first-child::before {
    grid-column: 1/2;
  }
  .full-width-split-screen > :not(img):last-child::before {
    grid-column: 4/5;
  }
}
.promo__boxes {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  margin-bottom: 5rem;
}
@media (max-width: 58.124375em) {
  .promo__boxes {
    flex-direction: column;
    align-items: center;
  }
}

.savingProgram__priceDrop {
  background-image: url("/img/savingPromo_price_drop_bkgd_1920x400.jpg");
}
.savingProgram__fullWidth {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.inStore__promo {
  margin-bottom: 5rem;
}

.advocacy {
  background-color: var(--advocacyRed);
  margin-bottom: 5rem;
}
.advocacy__text {
  font-size: 90px;
  line-height: 1;
